import { getDatabase, ref, set } from 'firebase/database';
import { canActivate, getAsync } from './firebase-async';
import { getAuth, createUserWithEmailAndPassword, signInWithEmailAndPassword } from "firebase/auth";

const auth = getAuth();
const db = getDatabase();

export const CurrentUser = async () => {
    await canActivate();
    return auth.currentUser;
}

export const RegisterAccount = (email, password, callback, errorCallback) => {
    createUserWithEmailAndPassword(auth, email, password)
        .then(AddUser)
        .then(callback)
        .catch(errorCallback);
}

export const LoginAccount = (email, password, callback, errorCallback) => {
    signInWithEmailAndPassword(auth, email, password)
        .then(AddUser)
        .then(callback)
        .catch(errorCallback);
}

export const Logout = async (callback, errorCallback) => {
    await canActivate();
    auth.signOut().then(callback).catch(errorCallback);
}

export const AddUser = () => {
    const currentUser = auth.currentUser;
    set(ref(db, `users/${currentUser.uid}/email`), currentUser.email);
}

export const Auth = async () => {
    const currentUser = auth.currentUser;
    await getAsync(ref(db, `users/${currentUser.uid}`))
        .then((snapshot) => {
            if (snapshot.exists()) {
                let userInfo = snapshot.val();
                localStorage.setItem("email", userInfo.email || false);
            } else
                throw new Error('nodata')
        })
        .catch(() => localStorage.removeItem("email"))

    await getAsync(ref(db, `isSuperAdmin/${currentUser.uid}`))
        .then((snapshot) => {
            if (snapshot.exists())
                localStorage.setItem("isSuperAdmin", snapshot.val() || false);
            else
                throw new Error('nodata')
        })
        .catch(() => localStorage.removeItem("isSuperAdmin"))

    await getAsync(ref(db, `isAdmin/${currentUser.uid}`))
        .then((snapshot) => {
            if (snapshot.exists())
                localStorage.setItem("isAdmin", snapshot.val() || false);
            else
                throw new Error('nodata')
        })
        .catch(() => localStorage.removeItem("isAdmin"))

    await getAsync(ref(db, `isActive/${currentUser.uid}`))
        .then((snapshot) => {
            if (snapshot.exists())
                localStorage.setItem("isActive", snapshot.val() || false);
            else
                throw new Error('nodata')
        })
        .catch(() => localStorage.removeItem("isActive"))
}