import { getAuth } from "firebase/auth";
import { DatabaseReference,DataSnapshot,onValue } from 'firebase/database';

const auth = getAuth();

export const canActivate = () => {
    return new Promise(resolve => {
        if (auth.currentUser) {
            resolve(true);
        } else {
            const unsubscribe = getAuth().onAuthStateChanged(function (user) {
                unsubscribe();
                if (user) {
                    resolve(true);
                } else {
                    resolve(false);
                }
            });
        }
    });
}

export const getAsync = (ref: DatabaseReference): Promise<DataSnapshot>=> {
    return new Promise((resolve, reject) => {
        function onError(error) {
            reject(error);
        } 

        function onData(snap) {
            resolve(snap);
        }
  
        onValue(ref, onData, onError, { onlyOnce: true });
    });
}