import React from 'react';
import { Link, useNavigate } from "react-router-dom";
import { LoginAccount } from '../../services/account-service';
import { NotificationManager} from 'react-notifications';

class Login extends React.Component{

    constructor(props){
        super(props)
        this.state = {
            email:"",
            password:"",
        }
    }

    EmailChange=(email)=>{
        this.setState({email:email.target.value});
    }

    PasswordChange=(password)=>{
        this.setState({password:password.target.value});
    }

    LoginClick=()=>{
        LoginAccount(
            this.state.email,
            this.state.password,
            async ()=>{
                NotificationManager.success('成功', '登入',3000);
                this.props.navigate('/join')
            },
            (error)=>{
                console.log(error);
                NotificationManager.error('帳號或密碼錯誤', '登入',3000);
            }
        )
    }

    render(){
        return (
            <div className="mx-auto" style={{maxWidth: '330px',width: '100%',padding:'50px 15px 15px 15px'}}>
                <div>
                    <p className="fs-2 text-center">登入</p>
                    <input className="form-control mb-2" type="email" value={this.state.email} onChange={this.EmailChange} placeholder="輸入您的電子郵件" />
                    <input className="form-control" type="password" value={this.state.password} onChange={this.PasswordChange} placeholder="輸入您的密碼" />
                    <div className="mt-3 d-flex justify-content-around">
                    <button className="btn btn-primary me-1 col-6" onClick={this.LoginClick}>登入</button>
                    <Link className="btn btn-primary col-6" to="/register">註冊</Link>
                    </div>
                </div>
            </div>
        )
    } 
}

export default function LoginNavigate(props) {
    const navigate = useNavigate();
  
    return <Login {...props} navigate={navigate} />;
  }